.tabs {
    margin-top: 2rem;
}
.tabs__buttons {
    display: flex;
    gap: 1rem;
    margin-bottom: 2rem;
}
.tabs__btn {
    padding: 1rem 2rem;
    background: var(--white);
    border-radius: 6rem;
    font-size: 1.6rem;
    cursor: pointer;
    transition: all 0.2s linear;
    font-family: 'FuturaPT';
    font-weight: 500;
    color: var(--default);
}
.tabs__btn.active {
    background: var(--blue);
    color: var(--white);
}
.tabs__tab {
    display: none;
}
.tabs__tab.active {
    display: block;
}

@media (hover:hover) {
    .tabs__btn:hover {
        background: var(--blue);
        color: var(--white);
    }
}
@media (hover:none) {
    .tabs__btn:active {
        background: var(--blue);
        color: var(--white);
    }
}


@media (max-width: 500px) {
    .tabs__buttons {
        overflow-x: auto;
    }
    .tabs__btn {
        flex-shrink: 0;
        flex-grow: 0;
        min-width: 10rem;
        padding: 1rem 1.5rem;
        font-size: 1.4rem;
    }
    .articles__list {
        grid-template-columns: repeat(1, 1fr);
        gap: 1rem;
    }
    .article__card {
        min-height: 20rem;
    }
}
